@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-image: url(/TomatoKingdom/static/media/bg.465a5482.jpg);
  background-size: cover;
  background-repeat: repeat;
  font-family: 'Baloo Tammudu 2', cursive;
}

p {
  margin: 0 0;
  padding: 0 0;
  line-height: 20px;
}

.App {
  text-align: center;
  font-family: 'Baloo Tammudu 2', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.Header {
    /* margin: 20px 0;
    margin-top: 40px; */
    width: 80%;
    /* min-width: 400px; */
    border: 1px solid #fff;
    border-radius: 15px;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
    font-size: 2rem;
    overflow: hidden;
    position: relative;
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(10px) saturate(100%) contrast(45%) brightness(130%);
            backdrop-filter: blur(10px) saturate(100%) contrast(45%) brightness(130%);
    content: '';
    animation: fadeIn 1s ease-in;
    padding: 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.logo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
}


.Logo {
    width: 30px;
    height: 30px;
    padding: 0 10px;
}

.wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center
}

.navigation 
{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@keyframes fadeIn {
    from {
        transform: translateY(-25px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}
.Button {
    padding: 15px 20px 10px 20px;
    font-size: 18px;
    background-color: #e04336;
    border-radius: 10px;
    /* border: 2px solid black; */
    border: none;
    color: white;
    cursor: pointer;
    line-height: 18px;
    font-family: 'Baloo Tammudu 2', cursive;
    margin: 5px 10px;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
    border-bottom: 3px solid #740e05;
}


.Button:active {
    border-bottom: none;
}


.Clock {
    
    font-size: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.Clock-wrapper {
    width: 200px;
    height: 200px;
    border: 5px solid rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center; 
    margin: 20px;   
}

.Clock-string {
    font-size: 20px;
    padding: 0 0;
    margin: 0 0;
    line-height: 20px;
}

.Clock-time {
    line-height: 60px;
    font-size: 60px;
    padding: 0 0;
    margin: 0 0;
}

@media only screen and (min-width: 768px){
    .Clock-wrapper {
        width: 300px;
        height: 300px;
        border: 5px solid rgba(0, 0, 0, 0.3);
        border-radius: 50%;
        display: flex;
        flex-direction:column;
        justify-content: center;
        align-items: center;
        margin: 20px;
    }
}
.Body {
    margin: 20px 0;
    width: 80%;
    /* height: 500px; */
    border: 1px solid #fff;
    border-radius: 15px;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
    font-size: 2rem;
    overflow: hidden;
    position: relative;
    background-color: rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(10px) saturate(100%) contrast(45%) brightness(130%);
    animation: fadeIn 1s ease-in;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 10px 10px;
}

.Body-wrapper {
    margin: 20px;
}
.Parallelogram {
    width: 50px;
    height: 10px;
    transform: skew(-30deg);
    background-color: white;
    border: 1px solid black;
    margin: 5px 5px;
  }

  .Stack-wrapper {
      width: 45%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
  }

  .Stack {

      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin: 20px 20px;
  }
.SoundCloudWidget {
    border-radius: 100px;
}
.Module {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 5px;
    flex-wrap: wrap;
}

.Setting-wrapper {
    width: 80%;
    margin: 30px 0;
}

.Input {
    height: 25px;
    border-radius: 5px;
    border: 1px solid black;
    outline: none;
    padding: 5px 5px;
    font-size: 15px;
    margin: 10px 30px 10px 0;
}

p {
    text-align: left;
}

.row::after {
    content: "";
    clear: both;
    display: table;
}


[class*="col-"] {
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

/* For mobile phones: */
[class*="col-"] {
    width: 100%;
    
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {
        width: 50%;
        margin-left: 25%;
    }
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}
}




